<template lang="html">
  <section class="slider">
    <Slides :image="images[chosenImage]"/>
    <div class="pagination">
      <div
        v-for="image in images" :key="image.id"
        @click="pagination(image.id)"
        class="pagination__dot">
      </div>
    </div>
  </section>
</template>

<script>
  import Slides from '@/components/slider/Slides'

  export default {
    name: "Home",
    components: {
      Slides
    },
    data() {
      return {
        images: [
          {
            id: 0,
            title: 'Ollios',
            subtitle: 'Newest furniture shop',
            url: 'background2.jpg'
          },
          {
            id: 1,
            title: 'Ollios',
            subtitle: 'The coolest furniture shop in the world',
            url: 'background.png'
          },
          {
            id: 2,
            title: 'Ollios',
            subtitle: 'Satisfaction of our clients is the most important thing',
            url: 'background3.jpg'
          }
        ],
        chosenImage: 0,
        intervalObject: null
      }
    },
    created() {
      this.intervalObject = setInterval(() => {
        this.move();
      }, 5000)
    },
    methods: {
      pagination(id) {
        this.chosenImage = id;
        clearInterval(this.intervalObject);
        this.intervalObject = setInterval(() => {
          this.move();
        }, 5000)
      },
      move() {
        let flag = this.chosenImage;
        flag++;
        if (flag >= this.images.length) {
          flag = 0;
        }
        this.chosenImage = flag;
      }
    }
  }

</script>

