    text-align: center;
    @media only screen and (max-width: 64em) {
      bottom: 12rem;
    }
    @media only screen and (max-width: 34.125em) {
      display: none;
    }

    &__dot {
      position: relative;
      width: 12px;
      height: 12px;
      border: 2px solid rgba(220, 220, 222, 1);
      border-radius: 100px;
      display: inline-block;
      cursor: pointer;
      margin: 0 4.5rem;
      transition: .3s;
      @media only screen and (max-width: 48em) {
        border: 2px solid var(--blue);
      }

      &:not(:last-child) {
        margin-right: 1rem;
      }

      &--active {
        border-color: var(--blue);
        background: var(--blue);
      }

      &:hover {
        transition: .3s;
        border-color: var(--blue);
        background: var(--blue);

        &:before {
          top: -48px;
          opacity: 1;
        }

        &:after {
          top: -18px;
          opacity: 1;
        }
      }
    }
  }
</style>
